const gravity = .25;
const colors = ['red', 'orange', 'yellow', 'lime', 'cyan', 'magenta', 'white'];

class Particle {
    constructor(x, y, xSpeed, ySpeed, pColor, size) {
        this.x = x;
        this.y = y;
        this.xSpeed = xSpeed;
        this.ySpeed = ySpeed;
        this.color = pColor;
        this.size = size;
        this.isAlive = true;
        this.trail = [];
        this.trailIndex = 0;
    }

    step(sketch) {
        this.trail[this.trailIndex] = sketch.createVector(this.x, this.y);
        this.trailIndex++;

        if (this.trailIndex > 10) {
            this.trailIndex = 0;
        }
        this.x += this.xSpeed;
        this.y += this.ySpeed;

        this.ySpeed += gravity;

        if (this.y > sketch.height) {
            this.isAlive = false;
        }
    }

    draw(sketch) {
        // this.drawTrail(sketch);
        sketch.fill(this.color);
        sketch.noStroke();
        sketch.rect(this.x, this.y, this.size, this.size);
    }

    drawTrail(sketch) {
        let index = 0;

        for (let i = this.trailIndex - 1; i >= 0; i--) {
            const tColor = sketch.lerpColor(sketch.color(this.color), sketch.color(64, 0), index / this.trail.length);
            sketch.fill(tColor);
            sketch.noStroke();
            sketch.rect(this.trail[i].x, this.trail[i].y, this.size, this.size);
            index++;
        }

        for (let i = this.trail.length - 1; i >= this.trailIndex; i--) {
            const tColor = sketch.lerpColor(sketch.color(this.color), sketch.color(64, 0), index / this.trail.length);

            sketch.fill(tColor);
            sketch.noStroke();
            sketch.rect(this.trail[i].x, this.trail[i].y, this.size, this.size);
            index++;
        }
    }
}

class Firework extends Particle {
    constructor(x, y, sketch) {
        super(x, y, sketch.random(-2, 2), sketch.random(-10, -15), sketch.random(colors), 10);
        this.countdown = sketch.random(30, 60);
    }

    step(sketch, fireworks) {
        super.step(sketch);

        this.countdown--;
        if (this.countdown <= 0) {
            const explosionSize = sketch.random(10, 25);
            for (let i = 0; i < explosionSize; i++) {

                const speed = sketch.random(5, 10);
                const angle = sketch.random(360);
                const xSpeed = sketch.cos(angle) * speed;
                const ySpeed = sketch.sin(angle) * speed;

                fireworks.push(new Particle(this.x, this.y,
                    xSpeed, ySpeed,
                    this.color, 5
                ));
            }
            this.isAlive = false;
        }
    }
}

export default Firework;