class Star {
    constructor(_x, _y, _r1, _r2, _npoints, _colr, _colg, _colb) {
        this.x = _x;
        this.y = _y;
        this.r1 = _r1;
        this.r2 = _r2;
        this.npoints = _npoints;
        this.colr = _colr;
        this.colg = _colg;
        this.colb = _colb;

        this.sx = 0;
        this.sy = 0;
        this.angle = 360 / _npoints;
        this.halfAngle = 360 / _npoints / 2;

    }
    createStar(sketch) {
        sketch.noStroke();
        sketch.fill(this.colr, this.colg, this.colb);
        sketch.beginShape();

        for (let j = 0; j < 360; j += this.angle) {
            this.sx = this.x + sketch.cos(j) * this.r2;
            this.sy = this.y + sketch.sin(j) * this.r2;
            sketch.vertex(this.sx, this.sy);
            this.sx = this.x + sketch.cos(j + this.halfAngle) * this.r1;
            this.sy = this.y + sketch.sin(j + this.halfAngle) * this.r1;
            sketch.vertex(this.sx, this.sy);
        }
        sketch.endShape(sketch.CLOSE);
    }

    moveStar(sketch, stars) {
        this.y += sketch.pow(this.r1, 0.9);

        if (this.y > sketch.height) {
            let index = stars.indexOf(this);
            stars.splice(index, 1);
        }
    }
}

export default Star;