import p5 from "p5";
import "p5/lib/addons/p5.sound";

// Loading effects
import Star from './loading/stars';

//visual effects
// import Glitch from './effects/glitch';
import Firework from './effects/fireworks';

const p5Sketch = (sketch, imageUrl) => {
    let mySound, fft, spectrum, height, width, img, canvas, amplitude;
    let pow2 = 512;
    let ampCutOut = 120
    let radius = 60;

    let songLoaded = false;
    let loading = false;

    let closed = false;

    let imgLoaded = false;

    //animation related
    let animationPick = Math.round(sketch.random(0, 1))

    // let glitch;
    // let glitchOn = false;
    // let glithCycles = 0;

    let progress = 0;
    let stars = []

    let fireworks = []

    sketch.playSong = songUrl => {
        if (mySound && mySound.url === songUrl) {
            if (songLoaded)
                sketch.canvasPressed()
            return false
        }

        songLoaded = false;
        loading = true;


        if (mySound)
            mySound.pause()

        animationPick = Math.round(sketch.random(0, 1))


        mySound = null;
        mySound = sketch.loadSound(songUrl,
            () => {
                songLoaded = true;
                loading = false;
                progress = 0;
                stars = [];
                fireworks = [];
                if (!closed)
                    mySound.play();
            },
            f => console.log("erÂ§or: " + f),
            p => {
                progress = p
            }
        );
    }

    sketch.stop = () => {
        mySound && mySound.pause();
        closed = true
    };

    sketch.canvasPressed = () => {
        if (!mySound)
            return null
        if (mySound.isPlaying()) mySound.pause();
        else mySound.play();
    }

    sketch.windowResized = () => {
        if (sketch.windowWidth < 500) {
            sketch.resizeCanvas(sketch.windowWidth * .7, sketch.windowWidth * .7);
            radius = 40
        }
        height = sketch.height;
        width = sketch.width;
    }

    //to run the glitch effect randomly
    // const resetCycle = () => {
    //     glithCycles = sketch.random(30, 50);
    //     setTimeout(resetCycle, sketch.random(5000, 10000))
    // }

    sketch.setup = () => {
        loading = true
        sketch.loadImage(imageUrl, function (res) {
            // glitch = new Glitch(res, sketch);
            img = res
            loading = false
            imgLoaded = true
        });

        canvas = sketch.createCanvas(350, 350);
        sketch.windowResized()
        canvas.mousePressed(sketch.canvasPressed);

        // canvas.mouseOver(() => glitchOn = true);
        // canvas.mouseOut(() => glitchOn = false);


        //set the parent to div #canvas
        canvas.parent('canvas')

        //set angle to degrees, for an easier circle
        sketch.angleMode(sketch.DEGREES);

        fft = new p5.FFT(0.7, pow2);
        amplitude = new p5.Amplitude();

        // resetCycle()
    }

    sketch.draw = () => {
        //just if the
        if (loading && !imgLoaded) {
            sketch.clear()
        }

        //show image
        if (imgLoaded) {
            sketch.image(img, 0, 0, sketch.width, sketch.height);
        }

        sketch.fill(255, 0, 0)

        if (songLoaded) {
            //dark overlay to see the effect better
            sketch.background('rgba(0,0,0, 0.25)');

            //get frecuencies
            spectrum = fft.analyze();

            //get aplitude
            let level = amplitude.getLevel();

            //this is so we start drawing from the center
            sketch.translate(width / 2, height / 2);

            for (let i = 1; i < 360 + 1; i += 5) {
                //Offset for the circle
                let r = spectrum[i] * .2
                r = r * ((Math.sqrt(-r + ampCutOut)) / Math.sqrt(ampCutOut - 10)) | 10

                //get base radius from aplitude
                let size = sketch.map(level, 0, 1, 0, 40) + radius;

                //Starting point for the circle
                let startX = sketch.cos(i) * (size)
                let startY = sketch.sin(i) * (size)

                //Ending point for the "circle"
                let x = sketch.cos(i) * (r + size)
                let y = sketch.sin(i) * (r + size)

                //draw white line
                sketch.stroke(255);
                sketch.line(startX, startY, x, y)
            }
        }
        //Show loading animation
        else if (loading) {
            // star animation
            if (animationPick === 0) {

                if (sketch.random(1) < .3 && fireworks.length < 30) {
                    fireworks.push(new Firework(sketch.random(1 * sketch.width / 4, 3 * sketch.width / 4), sketch.height, sketch));
                }

                fireworks.forEach((p) => {
                    p.step(sketch, fireworks);
                    p.draw(sketch);
                });

                fireworks = fireworks.filter((p) => p.isAlive);
            }
            //fireworks
            else if (animationPick === 1) {

                let s = new Star(
                    sketch.random(0, width), sketch.random(0, height / 2), sketch.random(2, 4), sketch.random(2, 7),
                    5,
                    17, 85, 79
                );
                for (let i = 0; i < sketch.random(3); i += 1) {
                    stars.push(s);
                }
                for (let star of stars) {
                    star.moveStar(sketch, stars);
                    star.createStar(sketch);
                }
            }

            //This is to draw a progress bar for when we are loading music
            if (progress > 0) {
                sketch.stroke(255);
                sketch.fill(255)
                sketch.rect(0, 4 * height / 5, progress * width, 30)
            }
        }
    }

}

export default p5Sketch;